function App() {
  const [account, setAccount] = useState('');
  const [provider, setProvider] = useState(null);
  const [signer, setSigner] = useState(null);
  const [fromToken, setFromToken] = useState({ address: 'ETH', symbol: 'ETH', decimals: 18, balance: '0' });
  const [toToken, setToToken] = useState({ address: '', symbol: '', decimals: 18, balance: '0' });
  const [fromAmount, setFromAmount] = useState('');
  const [toAmount, setToAmount] = useState('');
  const [fromTokenAddress, setFromTokenAddress] = useState('');
  const [toTokenAddress, setToTokenAddress] = useState('');
  const [slippage, setSlippage] = useState('0.5');
  const [showSettings, setShowSettings] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [showTokenModal, setShowTokenModal] = useState(false);
  const [isFromToken, setIsFromToken] = useState(true);
  const [needsApproval, setNeedsApproval] = useState(false);
  const [isCheckingApproval, setIsCheckingApproval] = useState(false);
  
  // 网络相关状态
  const [selectedNode, setSelectedNode] = useState('mevblocker');
  const [showNetworkDropdown, setShowNetworkDropdown] = useState(false);
  const [nodeStatus, setNodeStatus] = useState({});
  const [isCheckingNodes, setIsCheckingNodes] = useState(false);

  // 检查节点状态
  const checkNodeStatus = useCallback(async (nodeKey) => {
    try {
      const node = RPC_NODES[nodeKey];
      const tempProvider = new ethers.providers.JsonRpcProvider(node.url);
      
      // 测试连接性
      const blockNumber = await Promise.race([
        tempProvider.getBlockNumber(),
        new Promise((_, reject) => setTimeout(() => reject(new Error('Timeout')), 5000))
      ]);
      
      return blockNumber > 0;
    } catch (error) {
      console.error(`节点 ${nodeKey} 检查失败:`, error);
      return false;
    }
  }, []);

  // 批量检查所有节点状态
  const checkAllNodeStatus = useCallback(async () => {
    setIsCheckingNodes(true);
    const status = {};
    
    for (const nodeKey of Object.keys(RPC_NODES)) {
      status[nodeKey] = await checkNodeStatus(nodeKey);
    }
    
    setNodeStatus(status);
    setIsCheckingNodes(false);
  }, [checkNodeStatus]);

  // 切换节点
  const switchNode = useCallback(async (nodeKey) => {
    try {
      setError('');
      setSelectedNode(nodeKey);
      setShowNetworkDropdown(false);
      
      // 创建新的provider
      const node = RPC_NODES[nodeKey];
      let newProvider;
      
      if (account) {
        // 如果已连接钱包，优先使用MetaMask provider
        newProvider = new ethers.providers.Web3Provider(window.ethereum);
      } else {
        // 如果没连接钱包，使用RPC provider
        newProvider = new ethers.providers.JsonRpcProvider(node.url);
      }
      
      setProvider(newProvider);
      
      // 如果有账户，更新signer
      if (account) {
        setSigner(newProvider.getSigner());
      }
      
      setSuccess(`已切换到 ${node.name}`);
      setTimeout(() => setSuccess(''), 3000);
      
    } catch (error) {
      setError('切换节点失败: ' + error.message);
    }
  }, [account]);

  // 创建provider的函数
  const createProvider = useCallback((nodeKey) => {
    const node = RPC_NODES[nodeKey];
    return new ethers.providers.JsonRpcProvider(node.url);
  }, []);

  // 连接钱包
  const connectWallet = async () => {
    try {
      if (typeof window.ethereum !== 'undefined') {
        // 使用Web3Provider连接钱包，但可以配置RPC
        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
        const signer = provider.getSigner();
        
        setProvider(provider);
        setSigner(signer);
        setAccount(accounts[0]);
        
        // 获取ETH余额
        const balance = await provider.getBalance(accounts[0]);
        const ethBalance = ethers.utils.formatEther(balance);
        console.log('ETH余额:', ethBalance); // 调试日志
        
        // 立即更新余额
        setFromToken({ address: 'ETH', symbol: 'ETH', decimals: 18, balance: ethBalance });
        
        // 如果toToken是ETH，也更新余额
        if (toToken.address === 'ETH' || toToken.address === '') {
          setToToken(prev => ({ ...prev, balance: ethBalance }));
        }
        
        setError('');
        setSuccess(`钱包已连接，使用 ${RPC_NODES[selectedNode].name}`);
        setTimeout(() => setSuccess(''), 3000);
      } else {
        setError('请安装MetaMask钱包');
      }
    } catch (err) {
      setError('连接钱包失败: ' + err.message);
    }
  };

  // 获取代币信息
  const getTokenInfo = async (address, isFromToken = true) => {
    try {
      if (!provider || !account) return;
      
      // 验证地址格式
      if (!ethers.utils.isAddress(address)) {
        setError('无效的合约地址');
        return;
      }
      
      const contract = new ethers.Contract(address, ERC20_ABI, provider);
      const [name, symbol, decimals, balance] = await Promise.all([
        contract.name(),
        contract.symbol(),
        contract.decimals(),
        contract.balanceOf(account)
      ]);
      
      const tokenInfo = {
        address,
        name,
        symbol,
        decimals,
        balance: ethers.utils.formatUnits(balance, decimals)
      };
      
      if (isFromToken) {
        setFromToken(tokenInfo);
        setFromTokenAddress('');
      } else {
        setToToken(tokenInfo);
        setToTokenAddress('');
      }
      
      setError('');
    } catch (err) {
      setError('获取代币信息失败: ' + err.message);
    }
  };

  // 检查授权状态
  const checkApprovalStatus = async () => {
    if (!provider || !account || !fromAmount || fromToken.address === 'ETH') {
      setNeedsApproval(false);
      return;
    }

    setIsCheckingApproval(true);
    try {
      const tokenContract = new ethers.Contract(fromToken.address, ERC20_ABI, provider);
      const allowance = await tokenContract.allowance(account, UNISWAP_V2_ROUTER);
      const amountIn = ethers.utils.parseUnits(fromAmount, fromToken.decimals);
      
      setNeedsApproval(allowance.lt(amountIn));
    } catch (err) {
      console.error('检查授权状态失败:', err);
      setNeedsApproval(false);
    } finally {
      setIsCheckingApproval(false);
    }
  };

  // 获取交换价格
  const getSwapPrice = async () => {
    try {
      if (!provider || !fromAmount || !fromToken.address || !toToken.address) return;
      
      const router = new ethers.Contract(UNISWAP_V2_ROUTER, UNISWAP_V2_ROUTER_ABI, provider);
      const amountIn = ethers.utils.parseUnits(fromAmount, fromToken.decimals);
      
      let path;
      if (fromToken.address === 'ETH') {
        path = [WETH_ADDRESS, toToken.address];
      } else if (toToken.address === 'ETH') {
        path = [fromToken.address, WETH_ADDRESS];
      } else {
        path = [fromToken.address, WETH_ADDRESS, toToken.address];
      }
      
      const amounts = await router.getAmountsOut(amountIn, path);
      const amountOut = amounts[amounts.length - 1];
      
      setToAmount(ethers.utils.formatUnits(amountOut, toToken.decimals));
      
      // 获取价格后检查授权状态
      checkApprovalStatus();
    } catch (err) {
      console.error('获取价格失败:', err);
      setToAmount('');
    }
  };

  // 执行交换
  const executeSwap = async () => {
    try {
      if (!signer || !fromAmount || !toAmount) return;
      
      setLoading(true);
      setError('');
      setSuccess('');
      
      const router = new ethers.Contract(UNISWAP_V2_ROUTER, UNISWAP_V2_ROUTER_ABI, signer);
      const amountIn = ethers.utils.parseUnits(fromAmount, fromToken.decimals);
      const amountOutMin = ethers.utils.parseUnits(
        (parseFloat(toAmount) * (1 - parseFloat(slippage) / 100)).toString(),
        toToken.decimals
      );
      const deadline = Math.floor(Date.now() / 1000) + 60 * 20; // 20分钟
      
      let path;
      let tx;
      
      if (fromToken.address === 'ETH') {
        // ETH -> Token
        path = [WETH_ADDRESS, toToken.address];
        tx = await router.swapExactETHForTokens(
          amountOutMin,
          path,
          account,
          deadline,
          { value: amountIn }
        );
      } else if (toToken.address === 'ETH') {
        // Token -> ETH
        path = [fromToken.address, WETH_ADDRESS];
        
        // 检查并处理授权
        const tokenContract = new ethers.Contract(fromToken.address, ERC20_ABI, signer);
        const allowance = await tokenContract.allowance(account, UNISWAP_V2_ROUTER);
        
        // 如果当前授权不足，则授权最大值以支持后续交易
        if (allowance.lt(amountIn)) {
          setError('正在授权代币...');
          const approveTx = await tokenContract.approve(UNISWAP_V2_ROUTER, ethers.constants.MaxUint256);
          await approveTx.wait();
          setError('');
        }
        
        tx = await router.swapExactTokensForETH(
          amountIn,
          amountOutMin,
          path,
          account,
          deadline
        );
      } else {
        // Token -> Token
        path = [fromToken.address, WETH_ADDRESS, toToken.address];
        
        // 检查并处理授权
        const tokenContract = new ethers.Contract(fromToken.address, ERC20_ABI, signer);
        const allowance = await tokenContract.allowance(account, UNISWAP_V2_ROUTER);
        
        // 如果当前授权不足，则授权最大值以支持后续交易
        if (allowance.lt(amountIn)) {
          setError('正在授权代币...');
          const approveTx = await tokenContract.approve(UNISWAP_V2_ROUTER, ethers.constants.MaxUint256);
          await approveTx.wait();
          setError('');
        }
        
        tx = await router.swapExactTokensForTokens(
          amountIn,
          amountOutMin,
          path,
          account,
          deadline
        );
      }
      
      await tx.wait();
      setSuccess('交换成功！');
      
      // 立即刷新余额
      await refreshBalances();
      
      // 延迟一秒后再次刷新以确保区块链状态已更新
      setTimeout(async () => {
        await refreshBalances();
      }, 1000);
      
      setFromAmount('');
      setToAmount('');
      
    } catch (err) {
      setError('交换失败: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  // 刷新余额
  const refreshBalances = async () => {
    if (!provider || !account) return;
    
    try {
      // 获取ETH余额
      const ethBalance = await provider.getBalance(account);
      const formattedEthBalance = ethers.utils.formatEther(ethBalance);
      
      console.log('刷新余额 - ETH余额:', formattedEthBalance); // 调试日志
      
      // 刷新fromToken余额
      if (fromToken.address === 'ETH') {
        setFromToken(prev => ({ ...prev, balance: formattedEthBalance }));
        console.log('更新fromToken ETH余额:', formattedEthBalance);
      } else if (fromToken.address && ethers.utils.isAddress(fromToken.address)) {
        try {
          const contract = new ethers.Contract(fromToken.address, ERC20_ABI, provider);
          const balance = await contract.balanceOf(account);
          const formattedBalance = ethers.utils.formatUnits(balance, fromToken.decimals);
          setFromToken(prev => ({ ...prev, balance: formattedBalance }));
          console.log(`更新fromToken ${fromToken.symbol}余额:`, formattedBalance);
        } catch (err) {
          console.error('获取fromToken余额失败:', err);
        }
      }
      
      // 刷新toToken余额
      if (toToken.address === 'ETH') {
        setToToken(prev => ({ ...prev, balance: formattedEthBalance }));
        console.log('更新toToken ETH余额:', formattedEthBalance);
      } else if (toToken.address && ethers.utils.isAddress(toToken.address)) {
        try {
          const contract = new ethers.Contract(toToken.address, ERC20_ABI, provider);
          const balance = await contract.balanceOf(account);
          const formattedBalance = ethers.utils.formatUnits(balance, toToken.decimals);
          setToToken(prev => ({ ...prev, balance: formattedBalance }));
          console.log(`更新toToken ${toToken.symbol}余额:`, formattedBalance);
        } catch (err) {
          console.error('获取toToken余额失败:', err);
        }
      }
    } catch (err) {
      console.error('刷新余额失败:', err);
    }
  };

  // 交换代币位置
  const swapTokens = () => {
    const tempToken = fromToken;
    const tempAmount = fromAmount;
    const tempAddress = fromTokenAddress;
    
    setFromToken(toToken);
    setFromAmount(toAmount);
    setFromTokenAddress(toTokenAddress);
    
    setToToken(tempToken);
    setToAmount(tempAmount);
    setToTokenAddress(tempAddress);
    
    // 交换后清空toAmount，因为需要重新计算价格
    setToAmount('');
  };

  // 监听输入变化获取价格
  useEffect(() => {
    const timer = setTimeout(() => {
      if (fromAmount && fromToken.address && toToken.address) {
        getSwapPrice();
      }
    }, 500);
    
    return () => clearTimeout(timer);
  }, [fromAmount, fromToken.address, toToken.address]);

  // 监听账户变化和自动更新余额
  useEffect(() => {
    if (window.ethereum) {
      window.ethereum.on('accountsChanged', (accounts) => {
        if (accounts.length > 0) {
          setAccount(accounts[0]);
          refreshBalances();
        } else {
          setAccount('');
          setProvider(null);
          setSigner(null);
        }
      });
      
      window.ethereum.on('chainChanged', () => {
        window.location.reload();
      });

      // 添加区块监听以更新余额
      if (provider) {
        provider.on('block', () => {
          refreshBalances();
        });
      }

      // 设置定时刷新余额（每15秒）
      const intervalId = setInterval(() => {
        refreshBalances();
      }, 15000);

      // 清理函数
      return () => {
        if (provider) {
          provider.removeAllListeners('block');
        }
        clearInterval(intervalId);
        window.ethereum.removeAllListeners('accountsChanged');
        window.ethereum.removeAllListeners('chainChanged');
      };
    }
  }, [provider, account]); // 添加account作为依赖项

  // 当provider和account都准备好时，获取初始ETH余额
  useEffect(() => {
    const updateEthBalance = async () => {
      if (provider && account) {
        try {
          const balance = await provider.getBalance(account);
          const ethBalance = ethers.utils.formatEther(balance);
          console.log('更新ETH余额:', ethBalance); // 调试日志
          
          if (fromToken.address === 'ETH') {
            setFromToken(prev => ({ ...prev, balance: ethBalance }));
          }
          if (toToken.address === 'ETH') {
            setToToken(prev => ({ ...prev, balance: ethBalance }));
          }
        } catch (err) {
          console.error('获取ETH余额失败:', err);
        }
      }
    };
    
    updateEthBalance();
  }, [provider, account]);

  const handleTokenSelect = async (token) => {
    try {
      if (token.address === 'ETH') {
        const ethBalance = provider && account ? await provider.getBalance(account) : '0';
        const tokenInfo = {
          address: 'ETH',
          symbol: 'ETH',
          decimals: 18,
          balance: ethers.utils.formatEther(ethBalance)
        };
        if (isFromToken) {
          setFromToken(tokenInfo);
        } else {
          setToToken(tokenInfo);
        }
        console.log('选择ETH，余额:', ethers.utils.formatEther(ethBalance));
      } else {
        const contract = new ethers.Contract(token.address, ERC20_ABI, provider);
        const [name, symbol, decimals, balance] = await Promise.all([
          contract.name(),
          contract.symbol(),
          contract.decimals(),
          contract.balanceOf(account)
        ]);

        const formattedBalance = ethers.utils.formatUnits(balance, decimals);
        const tokenInfo = {
          address: token.address,
          name,
          symbol,
          decimals,
          balance: formattedBalance
        };

        if (isFromToken) {
          setFromToken(tokenInfo);
        } else {
          setToToken(tokenInfo);
        }
        
        console.log(`选择代币 ${symbol}，余额:`, formattedBalance);
      }
      setShowTokenModal(false);
      
      // 选择代币后刷新余额
      setTimeout(() => {
        refreshBalances();
      }, 100);
    } catch (err) {
      console.error('选择代币失败:', err);
      setError('选择代币失败: ' + err.message);
    }
  };

  const handlePercentageClick = (percentage) => {
    if (!fromToken.balance || !fromToken.decimals) return;
    
    const maxAmount = parseFloat(fromToken.balance);
    const amount = (maxAmount * percentage / 100).toFixed(6);
    setFromAmount(amount);
  };

  // 初始化效果 - 检查节点状态并设置默认provider
  useEffect(() => {
    // 检查所有节点状态
    checkAllNodeStatus();
    
    // 设置默认provider（使用选定的节点）
    if (!provider) {
      const defaultProvider = createProvider(selectedNode);
      setProvider(defaultProvider);
    }
  }, []);

  // 当选择的节点改变时，如果没连接钱包则更新provider
  useEffect(() => {
    if (!account && selectedNode) {
      const newProvider = createProvider(selectedNode);
      setProvider(newProvider);
    }
  }, [selectedNode, account, createProvider]);

  // 点击外部关闭下拉菜单
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (showNetworkDropdown && !event.target.closest('.network-selector')) {
        setShowNetworkDropdown(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [showNetworkDropdown]);

  return (
    <Container>
      <Card>
        <Header>
          <Title>Uniswap V2 Swap</Title>
          <SettingsButton onClick={() => setShowSettings(!showSettings)}>
            <FiSettings size={20} />
          </SettingsButton>
        </Header>

        {/* 网络选择器 - 在连接钱包前后都显示 */}
        <NetworkSelector className="network-selector">
          <NetworkButton 
            onClick={() => setShowNetworkDropdown(!showNetworkDropdown)}
          >
            <div>
              <div>{RPC_NODES[selectedNode].name}</div>
              <NetworkStatus>
                <StatusDot connected={nodeStatus[selectedNode]} />
                {nodeStatus[selectedNode] ? '已连接' : '连接失败'}
              </NetworkStatus>
            </div>
            <FiChevronDown />
          </NetworkButton>
          
          {showNetworkDropdown && (
            <NetworkDropdown>
              {Object.entries(RPC_NODES).map(([key, node]) => (
                <NetworkOption 
                  key={key}
                  onClick={() => switchNode(key)}
                >
                  <div>
                    <div>{node.name}</div>
                    <NetworkStatus>
                      <StatusDot connected={nodeStatus[key]} />
                      {isCheckingNodes ? '检查中...' : (nodeStatus[key] ? '正常' : '不可用')}
                    </NetworkStatus>
                  </div>
                  {selectedNode === key && <FiCheck />}
                </NetworkOption>
              ))}
              <NetworkOption 
                onClick={checkAllNodeStatus}
                style={{ 
                  borderTop: '1px solid #f0f0f0',
                  color: '#4c82fb',
                  fontWeight: '500'
                }}
              >
                <div style={{ display: 'flex', alignItems: 'center' }}>
                  <FiRefreshCw size={14} style={{ marginRight: '8px' }} />
                  刷新节点状态
                </div>
              </NetworkOption>
            </NetworkDropdown>
          )}
        </NetworkSelector>

        {!account ? (
          <ConnectButton onClick={connectWallet}>
            连接钱包
          </ConnectButton>
        ) : (
          <>
            <ConnectedAccount>
              已连接: {account.slice(0, 6)}...{account.slice(-4)}
            </ConnectedAccount>

            <TokenInput>
              <InputRow>
                <AmountInput
                  type="number"
                  placeholder="0.0"
                  value={fromAmount}
                  onChange={(e) => setFromAmount(e.target.value)}
                />
                <TokenSelector onClick={() => {
                  setIsFromToken(true);
                  setShowTokenModal(true);
                }}>
                  {fromToken.symbol || '选择代币'}
                </TokenSelector>
              </InputRow>
              <BalanceText>
                余额: {parseFloat(fromToken.balance).toFixed(6)}
              </BalanceText>
              <PercentageButtons>
                <PercentButton onClick={() => handlePercentageClick(25)}>25%</PercentButton>
                <PercentButton onClick={() => handlePercentageClick(50)}>50%</PercentButton>
                <PercentButton onClick={() => handlePercentageClick(75)}>75%</PercentButton>
                <PercentButton onClick={() => handlePercentageClick(100)}>MAX</PercentButton>
              </PercentageButtons>
            </TokenInput>

            <SwapIcon onClick={swapTokens}>
              <FiRefreshCw size={20} />
            </SwapIcon>

            <TokenInput>
              <InputRow>
                <AmountInput
                  type="number"
                  placeholder="0.0"
                  value={toAmount}
                  readOnly
                />
                <TokenSelector onClick={() => {
                  setIsFromToken(false);
                  setShowTokenModal(true);
                }}>
                  {toToken.symbol || '选择代币'}
                </TokenSelector>
              </InputRow>
              <BalanceText>
                余额: {parseFloat(toToken.balance).toFixed(6)}
              </BalanceText>
            </TokenInput>

            {/* 滑点设置 */}
            {showSettings && (
              <SlippageSettings>
                <SlippageTitle>滑点容忍度</SlippageTitle>
                <SlippageOptions>
                  <SlippageOption 
                    onClick={() => setSlippage('0.5')}
                    style={{ background: slippage === '0.5' ? '#4c82fb' : '#f8f9fa', color: slippage === '0.5' ? 'white' : '#333' }}
                  >
                    0.5%
                  </SlippageOption>
                  <SlippageOption 
                    onClick={() => setSlippage('1.0')}
                    style={{ background: slippage === '1.0' ? '#4c82fb' : '#f8f9fa', color: slippage === '1.0' ? 'white' : '#333' }}
                  >
                    1.0%
                  </SlippageOption>
                  <SlippageOption 
                    onClick={() => setSlippage('3.0')}
                    style={{ background: slippage === '3.0' ? '#4c82fb' : '#f8f9fa', color: slippage === '3.0' ? 'white' : '#333' }}
                  >
                    3.0%
                  </SlippageOption>
                  <CustomSlippageInput
                    type="number"
                    placeholder="自定义"
                    value={!['0.5', '1.0', '3.0'].includes(slippage) ? slippage : ''}
                    onChange={(e) => setSlippage(e.target.value)}
                  />
                </SlippageOptions>
              </SlippageSettings>
            )}

            <SwapButton 
              disabled={!fromToken.address || !toToken.address || !fromAmount || !toAmount || loading}
              onClick={executeSwap}
            >
              {loading ? '处理中...' : 
               isCheckingApproval ? '检查授权中...' :
               needsApproval ? `授权并交换 ${fromToken.symbol}` : 
               '交换'}
            </SwapButton>

            {error && <ErrorMessage>{error}</ErrorMessage>}
            {success && <SuccessMessage>{success}</SuccessMessage>}
          </>
        )}
      </Card>

      <TokenModal
        isOpen={showTokenModal}
        onClose={() => setShowTokenModal(false)}
        onSelectToken={handleTokenSelect}
        selectedTokenAddress={isFromToken ? fromToken.address : toToken.address}
        provider={provider}
        account={account}
      />
    </Container>
  );
}

export default App;
