import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { FiX, FiCopy } from 'react-icons/fi';
import { ethers } from 'ethers';

const ERC20_ABI = [
  "function name() view returns (string)",
  "function symbol() view returns (string)",
  "function decimals() view returns (uint8)",
  "function balanceOf(address) view returns (uint256)"
];

const ModalOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
`;

const ModalContainer = styled.div`
  background: #ffffff;
  border-radius: 20px;
  width: 100%;
  max-width: 420px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
`;

const ModalHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #e5e7eb;
`;

const Title = styled.h3`
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  color: #333333;
`;

const CloseButton = styled.button`
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  color: #666666;
  display: flex;
  align-items: center;
  justify-content: center;
  
  &:hover {
    color: #333333;
  }
`;

const SearchInput = styled.input`
  width: 100%;
  padding: 12px 16px;
  background: #f8f9fa;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  margin: 16px 0;
  font-size: 14px;
  color: #333333;
  
  &:focus {
    outline: none;
    border-color: #4c82fb;
  }
  
  &::placeholder {
    color: #999999;
  }
`;

const CommonTokens = styled.div`
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  padding: 0 20px;
  margin-bottom: 16px;
`;

const TokenButton = styled.button`
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 8px 12px;
  background: #f8f9fa;
  border: 1px solid #e5e7eb;
  border-radius: 16px;
  cursor: pointer;
  font-size: 14px;
  color: #333333;
  
  &:hover {
    background: #f0f2f5;
  }
  
  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
`;

const TokenList = styled.div`
  padding: 0 20px;
`;

const TokenItem = styled.button`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px;
  border: none;
  background: none;
  cursor: pointer;
  color: #333333;
  border-bottom: 1px solid #e5e7eb;
  
  &:hover {
    background: #f8f9fa;
  }
`;

const TokenInfo = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
`;

const TokenDetails = styled.div`
  text-align: left;
`;

const TokenSymbol = styled.div`
  font-weight: 500;
  color: #333333;
`;

const TokenAddress = styled.div`
  font-size: 12px;
  color: #666666;
  display: flex;
  align-items: center;
  gap: 4px;
  
  svg {
    cursor: pointer;
    color: #666666;
    
    &:hover {
      color: #333333;
    }
  }
`;

const TokenBalance = styled.div`
  font-size: 14px;
  color: #666666;
`;

const SearchStatus = styled.div`
  padding: 12px;
  margin: 8px 0;
  background: ${props => props.error ? '#fff2f2' : '#f8f9fa'};
  border: 1px solid ${props => props.error ? '#fecaca' : '#e5e7eb'};
  border-radius: 12px;
  color: ${props => props.error ? '#dc2626' : '#333333'};
  text-align: center;
`;

const commonTokens = [
  { symbol: 'ETH', address: 'ETH' },
  { symbol: 'WETH', address: '0xC02aaA39b223FE8D0A0e5C4F27eAD9083C756Cc2' },
  { symbol: 'USDT', address: '0xdAC17F958D2ee523a2206206994597C13D831ec7' },
  { symbol: 'USDC', address: '0xA0b86991c6218b36c1d19D4a2e9Eb0cE3606eB48' }
];

// 常用代币列表组件
const CommonTokensList = ({ tokens, onSelectToken, selectedTokenAddress, provider, account }) => {
  const [tokenBalances, setTokenBalances] = useState({});

  useEffect(() => {
    const fetchBalances = async () => {
      if (!provider || !account) return;

      const balances = {};
      
      for (const token of tokens) {
        try {
          if (token.address === 'ETH') {
            const balance = await provider.getBalance(account);
            balances[token.address] = ethers.utils.formatEther(balance);
          } else {
            const contract = new ethers.Contract(token.address, ERC20_ABI, provider);
            const balance = await contract.balanceOf(account);
            const decimals = await contract.decimals();
            balances[token.address] = ethers.utils.formatUnits(balance, decimals);
          }
        } catch (err) {
          console.error(`获取${token.symbol}余额失败:`, err);
          balances[token.address] = '0.00';
        }
      }
      
      setTokenBalances(balances);
    };

    fetchBalances();
  }, [tokens, provider, account]);

  const handleCopyAddress = (e, address) => {
    e.stopPropagation();
    navigator.clipboard.writeText(address);
  };

  return (
    <>
      {tokens.map(token => (
        <TokenItem key={token.address} onClick={() => onSelectToken(token)}>
          <TokenInfo>
            <TokenDetails>
              <TokenSymbol>{token.symbol}</TokenSymbol>
              <TokenAddress>
                {token.address === 'ETH' ? 'Native Token' : (
                  <>
                    {token.address.slice(0, 6)}...{token.address.slice(-4)}
                    <FiCopy
                      size={14}
                      onClick={(e) => handleCopyAddress(e, token.address)}
                    />
                  </>
                )}
              </TokenAddress>
            </TokenDetails>
          </TokenInfo>
          <TokenBalance>
            {token.address === selectedTokenAddress ? '已选择' : 
             parseFloat(tokenBalances[token.address] || '0').toFixed(6)}
          </TokenBalance>
        </TokenItem>
      ))}
    </>
  );
};

const TokenModal = ({ isOpen, onClose, onSelectToken, selectedTokenAddress, provider, account }) => {
  const [searchQuery, setSearchQuery] = useState('');
  const [searchedToken, setSearchedToken] = useState(null);
  const [searchError, setSearchError] = useState('');
  const [isSearching, setIsSearching] = useState(false);

  const searchToken = async (address) => {
    if (!provider || !ethers.utils.isAddress(address)) {
      setSearchError('无效的合约地址');
      setSearchedToken(null);
      return;
    }

    setIsSearching(true);
    setSearchError('');

    try {
      const contract = new ethers.Contract(address, ERC20_ABI, provider);
      const [name, symbol, decimals, balance] = await Promise.all([
        contract.name(),
        contract.symbol(),
        contract.decimals(),
        account ? contract.balanceOf(account) : 0
      ]);

      const tokenInfo = {
        address,
        name,
        symbol,
        decimals,
        balance: ethers.utils.formatUnits(balance, decimals)
      };

      setSearchedToken(tokenInfo);
      setSearchError('');
    } catch (err) {
      console.error('获取代币信息失败:', err);
      setSearchError('无效的代币合约');
      setSearchedToken(null);
    } finally {
      setIsSearching(false);
    }
  };

  useEffect(() => {
    if (!searchQuery) {
      setSearchedToken(null);
      setSearchError('');
      return;
    }

    const timer = setTimeout(() => {
      if (ethers.utils.isAddress(searchQuery)) {
        searchToken(searchQuery);
      } else {
        setSearchError('请输入有效的合约地址');
      }
    }, 300);

    return () => clearTimeout(timer);
  }, [searchQuery]);

  const handleCopyAddress = (e, address) => {
    e.stopPropagation();
    navigator.clipboard.writeText(address);
  };

  if (!isOpen) return null;

  return (
    <ModalOverlay onClick={onClose}>
      <ModalContainer onClick={e => e.stopPropagation()}>
        <ModalHeader>
          <Title>选择代币</Title>
          <CloseButton onClick={onClose}>
            <FiX size={20} />
          </CloseButton>
        </ModalHeader>
        
        <div style={{ padding: '20px' }}>
          <SearchInput
            placeholder="搜索代币名称或粘贴地址"
            value={searchQuery}
            onChange={e => setSearchQuery(e.target.value)}
          />
          
          <CommonTokens>
            {commonTokens.map(token => (
              <TokenButton
                key={token.address}
                onClick={() => onSelectToken(token)}
                disabled={token.address === selectedTokenAddress}
              >
                {token.symbol}
              </TokenButton>
            ))}
          </CommonTokens>
          
          <TokenList>
            {searchQuery && !searchedToken && !searchError && isSearching && (
              <SearchStatus>搜索中...</SearchStatus>
            )}
            
            {searchError && (
              <SearchStatus error>{searchError}</SearchStatus>
            )}
            
            {searchedToken && (
              <TokenItem onClick={() => onSelectToken(searchedToken)}>
                <TokenInfo>
                  <TokenDetails>
                    <TokenSymbol>{searchedToken.symbol}</TokenSymbol>
                    <TokenAddress>
                      {searchedToken.address.slice(0, 6)}...{searchedToken.address.slice(-4)}
                      <FiCopy
                        size={14}
                        onClick={(e) => handleCopyAddress(e, searchedToken.address)}
                      />
                    </TokenAddress>
                  </TokenDetails>
                </TokenInfo>
                <TokenBalance>
                  {parseFloat(searchedToken.balance).toFixed(6)}
                </TokenBalance>
              </TokenItem>
            )}
            
            {!searchQuery && <CommonTokensList 
              tokens={commonTokens} 
              onSelectToken={onSelectToken}
              selectedTokenAddress={selectedTokenAddress}
              provider={provider}
              account={account}
            />}
          </TokenList>
        </div>
      </ModalContainer>
    </ModalOverlay>
  );
};

export default TokenModal; 